.concept .container {
  padding: 182px 103px 90px;
  background: url("../img/concept-bg-1.png") 0 -284% no-repeat,
    url("../img/concept-bg-2.png") 100% 56.4% no-repeat;

  @include vp-1279 {
    background: url("../img/concept-1-mob.png") -90% -4% no-repeat,
      url("../img/concept-2-mob.png") 52% 29% no-repeat,
      url("../img/concept-3-mob.png") 53% 78% no-repeat;
  }

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    padding: 0 20px;
    width: 100%;
  }
}

.concept__first-screen {
  display: flex;
  margin-bottom: 335px;

  @include vp-1279 {
    padding-top: 40px;
    flex-direction: column;
  }

  @include vp-767 {
    margin-bottom: 315px;
  }
}

.concept h2 {
  @include font-settings ($ff-ttt, 800, 38px, 1, $color-default-white);
  text-transform: uppercase;
  letter-spacing: 0.02em;
  margin: 0 88px 0 0;

  @include vp-1279 {
    margin-bottom: 36px;
  }

  @include vp-767 {
    font-size: 18px;
    margin: 0 auto;
    margin-bottom: 36px;
    padding-top: 22px;
  }
}

.concept p {
  @include font-settings ($ff-mont, 500, 16px, 1.3, $color-default-white);
  margin: 0;
  z-index: 10;

  @include vp-767 {
    font-size: 12px;
  }
}

.concept__content {
  padding-top: 15px;
  padding-left: 7px;
}

.concept__content p {
  width: 744px;
  margin-bottom: 16px;

  @include vp-1279 {
    padding: 0;
    line-height: 1.4;
    width: 100%;
    margin-bottom: 17px;
  }
}

.concept__content b {
  @include font-settings ($ff-mont, 700, 16px, 1.3, $color-default-white);

  @include vp-767 {
    font-size: 12px;
  }
}

.concept__second-screen {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 308px;

  @include vp-767 {
    margin-bottom: 321px;
  }
}

.concept__second-screen p {
  padding-left: 105px;
  max-width: 520px;
  margin-bottom: 25px;

  @include vp-1279 {
    padding: 0;
    max-width: 100%;
    margin-bottom: 19px;
  }
}

.concept__third-screen {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-bottom: 74px;
  align-items: flex-end;

  &::after {
    position: absolute;
    content: url("../img/concept-bg-3.png");
    bottom: -350px;
    left: -150px;
    z-index: -10;
  }
}

.concept__third-screen p {
  padding-right: 70px;
  max-width: 563px;
  margin-bottom: 22px;

  @include vp-1279 {
    padding: 0;
    max-width: 100%;
    margin-bottom: 19px;
  }
}
